.input {
  height: 40px;
  width: 100%;
  font-size: 16px;
  background: transparent;
  color: #adadad;
  border: none;
  box-shadow: inset 4px 4px 5px rgb(0 0 0 / 16%), inset -4px -4px 5px rgb(255 255 255 / 6%);
  padding-left: 15px;
  border-radius: 10px;
}

.input:focus {
  outline: none;
}